<template>
  <div class="mob">
    <div class="swiper">
      <yd-slider autoplay="3000">
        <template v-for="(item,index) in config.swiper">
          <yd-slider-item>
            <a :href="item.link">
              <img :src="item.img">
            </a>
          </yd-slider-item>
        </template>
      </yd-slider>
      <div class="tabs">
        <div class="tab" v-for="(item,index) in config.tabs" :key="index" :class="{active:index==activeIndex}" @click="activeIndex=index">
          <p class="name">{{item.name}}</p>
        </div>
      </div>

      <div class="top-recom">
        <div class="recom" v-for="(item,index) in curTab.topDownload">
          <a :href="item.link" class="recom-link">
            <img :src="item.img" alt="" class="recom-link">
          </a>
        </div>
      </div>

      <div class="list">
        <div class="cell" v-for="(item,index) in curTab.list">
          <img :src="item.logo" alt="" class="logo">
          <div class="desc">
            <p class="title">{{item.name}}</p>
            <p class="detail">{{item.detail}}</p>
          </div>
          <div class="btns">
            <div class="android" @click="download(item)">安卓</div>
            <div class="ios" @click="download(item)">苹果</div>
          </div>
        </div>
      </div>

      <div class="footer" :style="'background:url('+config.footerImg+') 100% 100% no-repeat;'">
        <div class="qr-code">
          <div class="code-wrap" v-for="(item,index) in config.qrcodes">
            <img :src="item.img" alt="" class="">
            <p class="name">{{item.name}}</p>
          </div>
        </div>
        <p class="footer-info">{{config.footerInfo}}</p>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  props: ['config'],
  data() {
    return {
      activeIndex: 0
    }
  },
  computed: {
    curTab() {
      return this.config.tabs[this.activeIndex] || {}
    }
  },
  beforeRouteEnter: (to, from, next) => {
    next(() => {
      window._flexible.refreshRem()
    })
  },
  methods: {
    download(item) {
      if (window.navigator.userAgent.match(/iPhone|iPad|iPod/i)) {
        window.location.href = item.iosLink
        return
      }
      window.location.href = item.androidLink
    }
  }

};
</script>

<style lang="scss">
.mob {
  background: #f5f5f5;
  .yd-slider {
    // height: 150px;
  }
  .yd-slider-pagination-item-active {
    background-color: #fff;
  }
  .footer {
    padding: 30px 0;
    background: #000;
    .footer-info {
      font-size: 12px;
      color: #d6d6d6;
      letter-spacing: 0;
      line-height: 24px;
    }
    .qr-code {
      margin: 0 90px;
      display: flex;
      justify-content: space-between;
      margin-bottom: 12px;
    }
    .code-wrap {
      img {
        width: 76px;
        height: 76px;
      }
      font-size: 13px;
      color: #ffffff;
    }
  }
  .list {
    background: #fff;
    margin-bottom: 5px;
    .cell {
      display: flex;
      align-items: center;
      padding: 15px 12px 27px;
      text-align: left;
      border-bottom: 1px solid #e7e7e7;
      .btns {
        font-size: 14px;
        color: #ffffff;
        text-align: center;
        .android {
          width: 64px;
          height: 26px;
          line-height: 26px;
          background: #049bff;
          border-radius: 4px;
          margin-bottom: 19px;
        }
        .ios {
          width: 64px;
          height: 26px;
          line-height: 26px;
          background: #333333;
          border-radius: 4px;
        }
      }
      .desc {
        flex: 1;
        margin: 0 15px;
      }
      .detail {
        font-size: 13px;
        color: #888888;
      }
      .title {
        font-size: 16px;
        color: #000000;
        line-height: 21px;
      }
    }
    .logo {
      width: 70px;
      height: 70px;
      border-radius: 12px;
      border: 1px solid #eaeaea; /*no*/
    }
  }
  .top-recom {
    display: flex;
    justify-content: space-between;
    padding: 11px 14px 19px;
    background: #fff;
    margin-bottom: 12px;
    .recom-link {
      display: block;
      height: 150px;
      width: 106px;
    }
    .recom {
      width: 106px;
    }
  }
  .tabs {
    display: flex;
    height: 56px;
    box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.12);
    margin-bottom: 10px;
    .tab {
      display: flex;
      justify-content: center;
      align-items: center;
      flex: 1;
      font-size: 15px;
      color: #666666;
      .name {
        width: 100%;
        height: 21px;
        border-right: 1px dotted #e0e0e0;
      }
    }
    .active {
      position: relative;
      color: #269ee6;
      &:after {
        content: '';
        background: #269ee6;
        position: absolute;
        bottom: 0;
        width: 110px;
        height: 3px;
      }
    }
  }
}
</style>



