import Vue from 'vue';
import Router from 'vue-router';
import mob from '@/components/mob';
import pc from '@/components/pc';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'mob',
      component: mob
    },
    {
      path: '/pc',
      name: 'pc',
      component: pc
    }
  ]
});



