<template>
  <div id="app">
    <router-view :config="config" />
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      config: {
        rightQrcode: {
        },
        swiper: [
        ],
        tabs: [{
          name: '全部列表',
          topDownload: [
          ],
          list: []
        }, {
          name: '排行榜',
          topDownload: [
          ],
          list: []
        }, {
          name: '下载送币'
        }],
        footerImg: './static/imgs/footer.png',
        qrcodes: [
        ],
        footerInfo: '商务合作方式：13466581111     QQ群：239026794'
      }
    }
  },
  created() {
    window.vm = this
    this.jump()
    window.onresize = () => {
      window.vm.jump()
    }

    this.$http.get('./static/config/conf.json')
      .then((res) => {
        this.config = res.body
      })
  },
  methods: {
    jump() {
      if (this.checkua()) {
        this.$router.replace({ path: '/' })
        return
      }
      this.$router.replace({ path: '/pc' })
    },
    checkua() {
      const userAgent = window.navigator.userAgent
      const isMobile = {
        Android() {
          return !!userAgent.match(/Android/i)
        },
        BlackBerry() {
          return !!userAgent.match(/BlackBerry/i)
        },
        IOS() {
          return !!userAgent.match(/iPhone|iPad|iPod/i)
        },
        Windows() {
          return !!userAgent.match(/IEMobile/i)
        },
        any() {
          return (isMobile.Android() || isMobile.BlackBerry() || isMobile.IOS() || isMobile.Windows())
        }
      }
      return isMobile.any()
    }
  }
};
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
* {
  margin: 0;
  padding: 0;
}
</style>



