<template>
  <div class="pc">
    <div class="swiper">
      <yd-slider autoplay="3000">
        <template v-for="(item,index) in config.swiper">
          <yd-slider-item>
            <a :href="item.link">
              <img :src="item.img">
            </a>
          </yd-slider-item>
        </template>
      </yd-slider>
      <div class="container">

        <div class="right-qr">
          <img :src="config.rightQrcode.img" alt="">
          <p>扫一扫</p>
          <p>加入我们的矿山</p>
        </div>

        <div class="tabs">
          <div class="tab" v-for="(item,index) in config.tabs" :key="index" :class="{active:index==activeIndex}" @click="activeIndex=index">
            <p class="name">{{item.name}}</p>
          </div>
        </div>

        <!-- <div class="top-recom">
          <div class="recom" v-for="(item,index) in curTab.topDownload">
            <a :href="item.link" class="recom-link">
              <img :src="item.img" alt="" class="recom-link">
            </a>
          </div>
        </div> -->

        <div class="list">
          <div class="cell" v-for="(item,index) in curTab.list">
            <img :src="item.logo" alt="" class="logo">
            <img :src="item.cornerImg" alt="" class="corner" v-if="item.cornerImg">
            <div class="desc">
              <p class="title">{{item.name}}</p>
              <p class="detail">{{item.detail}}</p>
            </div>
            <div class="btns">
              <div class="android btn">安卓
                <div class="android-img download-img">
                  <img :src="item.androidImg" alt="" class="">
                  <p>安卓下载链接</p>
                </div>
              </div>

              <div class="ios btn">苹果
                <div class="ios-img download-img">
                  <img :src="item.iosImg" alt="" class="">
                  <p>苹果下载链接</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="footer-wrap" :style="'background:url('+config.footerImg+') 100% 100% no-repeat;'">
        <div class="footer">
          <div class="qr-code">
            <div class="code-wrap" v-for="(item,index) in config.qrcodes" v-if="item.img">
              <img :src="item.img" alt="" class="">
              <p class="name">{{item.name}}</p>
            </div>
          </div>
          <p class="footer-info">{{config.footerInfo}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['config'],
  data() {
    return {
      activeIndex: 0
    }
  },
  computed: {
    curTab() {
      return this.config.tabs[this.activeIndex]
    }
  },
  beforeRouteEnter: (to, from, next) => {
    setTimeout(() => {
      document.documentElement.style.fontSize = '37.5px'
    }, 500);
    next()
  },
  created() {
    document.documentElement.style.fontSize = '37.5px'
  },
  methods: {
    download(item) {
      if (window.navigator.userAgent.match(/iPhone|iPad|iPod/i)) {
        window.location.href = item.iosLink
        return
      }
      window.location.href = item.androidLink
    }
  }

};
</script>

<style lang="scss">
.pc {
  margin: 0 auto;
  background: #f5f5f5;
  .container {
    position: relative;
    margin: 0 auto;
    width: 990px;
    .right-qr {
      position: absolute;
      right: -147px;
      top: 0;
      width: 106px;
      line-height: 18px;
      padding: 13px;
      font-size: 13px;
      color: #888888;
      background: #fff;
      img {
        width: 106px;
      }
    }
  }
  .tabs {
    width: 100%;
    height: 56px;
    background: #fff;
  }
  .yd-slider-pagination {
    bottom: 20px;
  }
  .yd-slider-pagination-item-active {
    background-color: #fff;
  }
  .yd-slider-pagination-item {
    width: 12px;
    height: 12px;
    margin: 8px;
  }
  .footer-wrap {
    margin-top: 100px;
    width: 100%;
  }
  .footer {
    width: 400px;
    margin: 0 auto;
    padding: 30px 0;
    .footer-info {
      font-size: 12px;
      color: #d6d6d6;
      letter-spacing: 0;
      line-height: 24px;
    }
    .qr-code {
      margin: 0 90px;
      display: flex;
      justify-content: space-between;
      margin-bottom: 12px;
    }
    .code-wrap {
      img {
        width: 76px;
        height: 76px;
      }
      font-size: 13px;
      color: #ffffff;
    }
  }
  .list {
    margin-bottom: 5px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .cell {
      position: relative;
      width: 30%;
      display: flex;
      align-items: center;
      background: #fff;
      padding: 15px 12px 27px;
      text-align: left;
      margin-top: 20px;
      box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.14);
      .corner {
        position: absolute;
        height: 25px;
        left: 7px;
        top: -4px;
      }
      .btns {
        font-size: 14px;
        color: #ffffff;
        text-align: center;
        .download-img {
          padding: 5px;
          background: #fff;
          display: none;
          font-size: 14px;
          color: #565656;
          border: 1px solid #e3e3e3;
          box-shadow: 0 2px 8px 0 rgba(171, 171, 171, 0.5);
          img {
            width: 80px;
            height: 80px;
          }
        }
        .btn {
          position: relative;
          width: 64px;
          height: 26px;
          line-height: 26px;
          border-radius: 4px;
        }
        .android {
          background: #049bff;
          margin-bottom: 19px;
          &:hover .android-img {
            position: absolute;
            z-index: 99;
            right: -100px;
            top: -20px;
            display: block;
          }
        }
        .ios {
          width: 64px;
          height: 26px;
          line-height: 26px;
          background: #333333;
          border-radius: 4px;
          &:hover .ios-img {
            position: absolute;
            z-index: 99;
            right: -100px;
            top: -20px;
            display: block;
          }
        }
      }
      .desc {
        flex: 1;
        margin: 0 15px;
      }
      .detail {
        font-size: 13px;
        color: #888888;
      }
      .title {
        font-size: 16px;
        color: #000000;
        line-height: 21px;
      }
    }
    .logo {
      width: 70px;
      height: 70px;
      border-radius: 12px;
      border: 1px solid #eaeaea;/*no*/
    }
  }
  .top-recom {
    display: flex;
    justify-content: space-between;
    padding: 11px 14px 19px;
    background: #fff;
    margin-bottom: 12px;
    .recom-link {
      display: block;
      height: 150px;
      width: 106px;
    }
    .recom {
      width: 106px;
    }
  }
  .tabs {
    display: flex;
    height: 56px;
    box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.12);
    .tab {
      position: relative;
      width: 113px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 15px;
      color: #666666;
      .name {
        width: 100%;
        height: 21px;
        border-right: 1px dotted #e0e0e0;
      }
    }
    .active {
      color: #269ee6;
      &:after {
        content: '';
        background: #269ee6;
        position: absolute;
        bottom: 0;
        width: 110px;
        height: 3px;
      }
    }
  }
}
</style>



